import Color from 'color';
import DPColors from './DPColors';

export default class AdminThemeColors {
	constructor(config) {
		if (config.dpDebugEn) console.log('AdminThemeColors');
		if (config.adminColors && config.adminColors.rootSelector && config.adminColors.colors && config.adminColors.colors.length >= 3) {
			const jRootDiv = jQuery(config.adminColors.rootSelector);
			if (jRootDiv && jRootDiv.length) {
				const adminColorsObj = [];
				for (let i = 0; i < 4; i++) {
					const colorIdx = i + 1;
					if (config.adminColors.colors[i]) {
						const adminColor = Color.rgb(config.adminColors.colors[i]);
						if (adminColor) {
							adminColorsObj.push(adminColor);
							jRootDiv[0].style.setProperty(`--dpu-color-${colorIdx}`, `${adminColor.red()}, ${adminColor.green()}, ${adminColor.blue()}`);
							if (colorIdx === 1) {
								const bgAccent = adminColor.lightness(95);
								jRootDiv[0].style.setProperty(`--dpu-color-${colorIdx}-light`, `${bgAccent.red()}, ${bgAccent.green()}, ${bgAccent.blue()}`);
							}
							const textColor = DPColors.getContrastColor(config.adminColors.colors[i]);
							jRootDiv[0].style.setProperty(`--dpu-color-${colorIdx}-text`, `${textColor.red()}, ${textColor.green()}, ${textColor.blue()}`);
						}
					}
				}
				if (adminColorsObj && config.adminColors.addRgbVars) {
					['r', 'b', 'g'].forEach((rgbIdx) => {
						let color = DPColors.getMostColor(adminColorsObj, rgbIdx);
						if (config.adminColors.rgbFeatureThresh && config.adminColors.rgbDefaults && config.adminColors.rgbDefaults[rgbIdx]) {
							const feature = DPColors.getRgbHueFeature(rgbIdx, color);
							//console.log('Color: ', color, 'RGB:', rgbIdx, 'Feature:', feature);
							if (feature < config.adminColors.rgbFeatureThresh) {
								/*if (rgbIdx !== 'r') color = DPColors.getRgbHueAdjustedColor(rgbIdx, color);
								else color = Color(config.adminColors.rgbDefaults[rgbIdx]);*/
								//color = Color(config.adminColors.rgbDefaults[rgbIdx]);
								color = Color(config.adminColors.rgbDefaults[rgbIdx]);
							}
						}
						jRootDiv[0].style.setProperty(`--dpu-color-${rgbIdx}`, `${color.red()}, ${color.green()}, ${color.blue()}`);
						const textColor = DPColors.getContrastColor(color);
						jRootDiv[0].style.setProperty(`--dpu-color-${rgbIdx}-text`, `${textColor.red()}, ${textColor.green()}, ${textColor.blue()}`);
						const index = adminColorsObj.indexOf(color);
						if (index !== -1) {
							adminColorsObj.splice(index, 1);
						}
					});
				}
			}
		}
	}
}
